import React from 'react';
import './app.scss';

import {
	BrowserRouter as Router,
	Redirect,
	Switch,
} from 'react-router-dom';

import * as Routes from './routes';
import { HomePage, AboutPage, WorkPage, ContactPage } from './pages';
import { LocaleProvider, WorkProvider } from './services';
import { RouteWithLayout } from './utilities';
import { BaseLayout } from './layouts';

function App() {
	return (
		<div className="app">
			<div className="overlayTexture"></div>
			<LocaleProvider>
				<WorkProvider>
					<Router basename="/portfolio">
						<Switch>
							<RouteWithLayout
								exact
								path={Routes.LANDING}
								component={HomePage}
								layout={BaseLayout}
							/>
							<Redirect from={Routes.HOME} to={Routes.LANDING} />
							<RouteWithLayout
								exact
								path={'/portfolio' + Routes.ABOUT}
								component={AboutPage}
								layout={BaseLayout}
							/>
							<RouteWithLayout
								exact
								path={Routes.WORK}
								component={WorkPage}
								layout={BaseLayout}
							/>
							<RouteWithLayout
								exact
								path={Routes.CONTACT}
								component={ContactPage}
								layout={BaseLayout}
							/>
						</Switch>
					</Router>
				</WorkProvider>
			</LocaleProvider>
		</div>
	);
}

export default App;
