import React, { useState, useEffect, useRef } from 'react';
import classnames from 'classnames';
import { Showcase } from './Showcase';
import { useWork } from '../../services';

export const ShowcaseGroup = ({}) => {
	const { allWork } = useWork();

	return (
		<div className="showcaseGroup">
			{allWork && allWork.map((work, i) => (
				<Showcase key={work.id} data={work} globalInfo={{index: i, total: allWork.length}} />
			))}
		</div>
	);
};
