import {
	default as React,
	useContext,
	createContext,
} from 'react';

const WorkContext = createContext();
const useWork = () => useContext(WorkContext);

const WorkProvider = ({ children }) => {
	const allWork = [
		{
			id: Math.random() * Date.now(),
			img: 'https://images.unsplash.com/photo-1522542550221-31fd19575a2d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80',
			name: 'Example 1',
			links: {
				github: 'https://github.com/Thomva',
				demo: 'https://google.com',
			},
		},
		{
			id: Math.random() * Date.now(),
			img: 'https://images.unsplash.com/photo-1460925895917-afdab827c52f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1302&q=80',
			name: 'Example 2',
			links: {
				github: 'https://github.com/Thomva',
				demo: 'https://google.com',
			},
		},
		{
			id: Math.random() * Date.now(),
			img: 'https://images.unsplash.com/photo-1530435460869-d13625c69bbf?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80',
			name: 'Example 3',
			links: {
				github: 'https://github.com/Thomva',
				demo: 'https://google.com',
			},
		},
	]

	const exports = {
		allWork,
	};

	return (
		<WorkContext.Provider value={exports}>{children}</WorkContext.Provider>
	);
};

export { WorkContext, WorkProvider, useWork };
