import React from 'react';
import classnames from 'classnames';
import { FaGithub, FaLink } from 'react-icons/fa';

export const Showcase = ({ classes, data, globalInfo }) => {
	return (
		<div className={classnames('showcase', classes)}>
			<img
				src={data && data.img}
				alt={data && data.name}
				className="showcase__image"
			/>
			<div className="showcase__index">
				{globalInfo.index + 1} / {globalInfo.total}
			</div>
			<div className="showcase__infoContainer">
				<div className="showcase__info">
					<h2 className="showcase__name">{data.name}</h2>
					<p className="showcase__general">
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Veniam
						nobis blanditiis, dignissimos, tempore laborum iste porro ipsa
						dolorem at nihil iure quisquam est officia quaerat ipsam fuga, non
						ipsum assumenda!
					</p>
					<p className="showcase__general">
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Veniam
						nobis blanditiis, dignissimos, tempore laborum iste porro ipsa
						dolorem at nihil iure quisquam est officia quaerat ipsam fuga, non
						ipsum assumenda!
					</p>
					<p className="showcase__general">
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Veniam
						nobis blanditiis, dignissimos, tempore laborum iste porro ipsa
						dolorem at nihil iure quisquam est officia quaerat ipsam fuga, non
						ipsum assumenda!
					</p>
					<p className="showcase__general">
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Veniam
						nobis blanditiis, dignissimos, tempore laborum iste porro ipsa
						dolorem at nihil iure quisquam est officia quaerat ipsam fuga, non
						ipsum assumenda!
					</p>
					<p className="showcase__general">
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Veniam
						nobis blanditiis, dignissimos, tempore laborum iste porro ipsa
						dolorem at nihil iure quisquam est officia quaerat ipsam fuga, non
						ipsum assumenda!
					</p>
				</div>
				<div className="showcase__links">
					<a href={data.links.github} className="link showcase__link" target="_blank">
						<FaGithub />
					</a>
					<a href={data.links.demo} className="link showcase__link" target="_blank">
						<FaLink />
					</a>
				</div>
			</div>
		</div>
	);
};
